.container {
  background-color: black;
  max-width: 100%;
  max-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 10;
  padding: 20px 0;
}

.container a {
  font-family: "DM Sans";
  text-decoration: none;
  color: white;
  font-size: 16px;
}

.navbar {
  position: sticky;
  top: 0;
  max-width: 60%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.navbar a {
  margin-left: 20px;
}

.logo {
  max-width: 40%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.inativo {
  display: none;
}

.legenda {
  color: white;
  position: relative;
  top: 20px;
  right: 60px;
}

@media (max-width: 700px) {
  .container a {
    font-size: 10px;
  }
  .navbar {
    max-width: 100%;
  }
  .logo_i {
    width: 30px !important;
  }
}
